import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      isRegAuth: state => state.user.isRegAuth,
      isReg: state => state.user.isReg
    })
  },
  methods: {
    setType (type) {
      this.$store.commit('SET_REG_TYPE', type)

      const role = (type === 'student') ? '1' : '2' // 角色 ，学生为1，教师为2
      // this.$router.push({ name: 'register' })
      location.href = 'http://www.zhiheyikaoqin.cn/sdc/wechat/authorize?returnUrl=index.html&role=' + role + '&path=register'
    }
  }
}



